import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import DialogContentText from "@mui/material/DialogContentText";

const dispatchedEvents = [];

const SrsMuiDialogContentText = function (props, ref) {
  return <DialogContentText {...props} theme={props.muiTheme}>{props.content}</DialogContentText>;
};

const styles = `
  /* Custom styles for the MuiDialogContentText component */
`;

const props = [
  {
    name: "content",
    defaultValue: "",
    type: "string",
  },
  {
    name: "color",
    defaultValue: "initial",
    type: "string",
  },
  {
    name: "component",
    defaultValue: "div",
    type: "string",
  },
  {
    name: "id",
    defaultValue: "",
    type: "string",
  },
  {
    name: "variant",
    defaultValue: "body1",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to DialogContentText if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiDialogContentText),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
